import { observer } from 'mobx-react-lite';
import React, { useCallback, useEffect } from 'react';
import gameStore from '../utils/gameStore';
import Title from 'antd/es/typography/Title';
import { Button } from 'antd';
import { connect, updateRotation } from '../utils/data';
import { Rotation } from '../utils/interfaces';

const Connector: React.FC = observer(() => {
	const { gameID, setGameId, setRole, data } = gameStore;

	const changeGameId = useCallback(
		(value: string) => {
			setGameId(value);
		},
		[setGameId]
	);

	useEffect(() => {
		const rotate = (event: DeviceOrientationEvent) => {
			console.log(event);
			const rotation: Rotation = {
				x: event.alpha || 0,
				y: event.beta || 0,
				z: event.gamma || 0,
			};
			updateRotation(gameID, rotation);
		};
		if (window.DeviceOrientationEvent) {
			window.addEventListener('deviceorientation', rotate);
		} else {
			console.log('     .');
		}
	}, []);

	return (
		<>
			<Title editable={{ onChange: changeGameId }}>{gameID}</Title>
			<Button
				disabled={data.roles?.receiver}
				onClick={() => connect(gameID, 'receiver', setRole)}
			>
				Receiver
			</Button>
			<Button
				disabled={data.roles?.transmitter}
				onClick={() => connect(gameID, 'transmitter', setRole)}
			>
				Transmitter
			</Button>
		</>
	);
});

export default Connector;
