import { onValue, ref, set } from 'firebase/database';
import database from './firebase';
import { Role, Roles, Rotation } from './interfaces';

export const connect = (
	gameID: string,
	role: Role,
	setRole: (role: Role) => void
) => {
	try {
		set(ref(database, `${gameID}/roles/${role}`), true);
		setRole(role);
	} catch (error) {
		console.log(error);
	}
};

export const updateRotation = (gameID: string, rotation: Rotation) => {
	try {
		set(ref(database, `${gameID}/rotation/`), rotation);
	} catch (error) {
		console.log(error);
	}
};

export const getData = async (
	gameID: string,
	role: Role,
	setRotation: (value: Rotation) => void,
	setRoles: (roles: Roles) => void
) => {
	onValue(ref(database, `${gameID}`), (snapshot) => {
		const data = snapshot.val();
		console.log(data);
		setRoles(data?.roles);
		setRotation(data?.rotation);
	});
};
