import { makeAutoObservable } from 'mobx';
import { Data, Role, Roles, Rotation } from './interfaces';

class GameStore {
	// gameID: string =
	// 	sessionStorage.getItem('gameID') ||
	// 	Math.floor(Math.random() * 900 + 100).toString();
	gameID: string = '228';
	role: Role = null;
	data: Data = {};

	setRole = (role: Role) => {
		this.role = role;
	};

	setRotation = (rotation: Rotation) => {
		this.data = { ...this.data, rotation: rotation };
	};

	setRoles = (roles: Roles) => {
		this.data = { ...this.data, roles: roles };
	};

	setGameId = (id: string) => {
		if (!isNaN(Number(id)) && id.length === this.gameID.length) {
			this.gameID = id;
			sessionStorage.setItem('gameId', id);
			return id;
		}
		return this.gameID;
	};

	constructor() {
		makeAutoObservable(this);
	}
}

const gameStore = new GameStore();

export default gameStore;
