import { observer } from 'mobx-react-lite';
import React, { CSSProperties, useMemo } from 'react';
import gameStore from '../utils/gameStore';
import constants from '../utils/constants';
import { Typography } from 'antd';

const Cube: React.FC = observer(() => {
	const { data, role } = gameStore;

	const showCube = useMemo(
		() =>
			data?.roles?.transmitter && data?.roles.receiver && role === 'receiver',
		[data, role]
	);

	const showRotation = useMemo(
		() =>
			data?.roles?.transmitter &&
			data?.roles.receiver &&
			role === 'transmitter',
		[data, role]
	);

	const sideStyle: CSSProperties = {
		position: 'absolute',
		marginInlineStart: `${-constants.sideSize / 4}em`,
		width: `${constants.sideSize / 2}em`,
		height: `${constants.sideSize / 2}em`,
		backgroundColor: 'rgba(255, 0, 0, 0.5)',
		border: '1px solid black',
	};

	return (
		<>
			{showCube && (
				<div
					style={{
						transformOrigin: 'center center',
						transform: `rotateX(${data?.rotation?.x}deg) rotateY(${data?.rotation?.y}deg) rotateZ(${data?.rotation?.z}deg)`,
					}}
				>
					<div style={{ ...sideStyle, transform: `translateZ(50px)` }} />
					<div
						style={{
							...sideStyle,
							transform: `rotateY(180deg) translateZ(calc(${
								constants.sideSize / 2
							}em))`,
						}}
					/>

					<div
						style={{
							...sideStyle,
							transform: `rotateX(90deg) translateZ(calc(${
								constants.sideSize / 2
							}em))`,
						}}
					/>
					<div
						style={{
							...sideStyle,
							transform: `rotateX(-90deg) translateZ(calc(${
								constants.sideSize / 2
							}em))`,
						}}
					/>
					<div
						style={{
							...sideStyle,
							transform: `rotateY(-90deg) translateZ(calc(${
								constants.sideSize / 2
							}em))`,
						}}
					/>
					<div
						style={{
							...sideStyle,
							transform: `rotateY(90deg) translateZ(calc(${
								constants.sideSize / 2
							}em))`,
						}}
					/>
				</div>
			)}
			{true && (
				<>
					<Typography.Text>x: {data?.rotation?.x}</Typography.Text>
					<Typography.Text>y: {data?.rotation?.y}</Typography.Text>
					<Typography.Text>z: {data?.rotation?.z}</Typography.Text>
				</>
			)}
		</>
	);
});

export default Cube;
